<script>
	import tou_hand1 from '../../components/tou_hand1/tou_hand1.vue'
	
	export default {
		components: {tou_hand1},
		mounted() {
			
		},

		data() {
			return {
				item:{}
			}
		},
		onLoad(option) {
			this.item=option
			this.item.insureStartTime=this.timestr(option.insureStartTime)
			this.item.insureEndTime=this.timestr(option.insureEndTime)
			console.log('777',option)
		},
		methods: {
			timestr(dateStr){
				const date = new Date(Date.parse(dateStr));
				const year = date.getFullYear();
				const month = date.getMonth() + 1;
				const day = date.getDate();
				const hour = date.getHours();
				const minute = date.getMinutes();
				const second = date.getSeconds();
				const formatStr = `${year}年${month}月${day}日${hour}时${minute}分${second}秒`;
				return formatStr
			}
			
		}
	}
</script>

